/* Generals */
.title {
  text-align: center;
}

.bigText {
  font-size: 45px;
  padding: 6px 0;
}

.midText {
  font-size: 26px;
  padding: 6px 0;
  font-weight: normal;
}

.smallText {
  font-size: 20px;
  padding: 6px 0;
}

.smallerText {
  font-size: 16px;
  padding: 6px 0;
}

.container {
  padding: 18px 28px;
}



/* App */
.App {
  margin: 0 auto;
  max-width: 2000px;
}


/* MAIN APP STYLES */

/* Nav Section */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  width: 100%;
  z-index: 1;
  /* background: green; */
}
nav > h3 {
  text-transform: uppercase;
  color: red;
  font-size: 35px;
}

nav > a {
  background: red;
  color: #fff;
  padding: 6px 15px;
  border-radius: 3px;
}

/* Banner Section */
.banner {
  min-height: 100vh;
  width: 100%;
  background: url('./assets/images/netflix_home_page_image.jpg');
  background-position: top left;
  background-size: cover;
}

.banner .linearBgChange {
  width: 100%;
  min-height: 100vh;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.9));
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Email Section */
.email .grid2 {
  /* background: red; */
  display: grid;
  width: 60%;
  margin: 0 auto;
  grid-template-columns: auto minmax(100px, 200px);
}

.email [type=text] {
  padding: 14px 6px;
  font-size: 17px;
  outline: none;
}

.email a {
  padding-top: 14px;
  padding-bottom: 14px;
  background: red;
  color: #fff;
  font-size: 20px;
}

/* main sections */
main > section {
  border-top: 8px solid #222222;
  padding-top: 55px !important;
  padding-bottom: 55px !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-items: center;
}

main > section img {
  width: 520px;
}

/* FAQ Sections */
.faq {
  border-top: 8px solid #222222;
  padding-top: 55px !important;
  padding-bottom: 55px !important;
}

.accordion {
  width: 70%;
  margin: 5px auto !important;
  color: white;
}

.accordion > div {
  background-color: #303030;
  margin: 2px 0;
}

.accordion .accordion-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.accordion .accordion-top button {
  background: none;
  color: white;
  font-size: 25px;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
}

/* .accordion .accordion-body {
  pad
} */

/* Footer */
footer {
  border-top: 8px solid #222222;
  padding-top: 55px !important;
  padding-bottom: 55px !important;
}

footer > div {
  width: 70%;
  margin: 0 auto;
  color: #5b5b5b;
}

footer section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 210px));
  margin-top: 15px;
  margin-bottom: 15px;
}

.footer-links ul {
  list-style: none;
}

.footer-links ul > li {
  padding: 6px 0;
}

.footer-links ul a {
  color: #5b5b5b;
  padding: 12px 0 !important;
}




